import React, {useCallback} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";
import {useForm} from "react-hook-form";
import {StakeButtonWidget} from "./button";
import {useBalance} from "../../../utils/token_server";
import {useStakeContext} from "../../../provider/stake_provider";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import BigNumber from "bignumber.js";
import {useSendModal} from "../../../widget/modal/send_modal";
import {useDeposit} from "../../../server/chef";
import {InputWidget} from "../../../widget/base/input";
import {ButtonWidget} from "../../../widget/base/button";
import {BackWidget} from "./back_widget";

export const BottomRight: React.FC = () => {
    const {register, errors, setValue, handleSubmit} = useForm();

    const {contract, pid, address, deposits, totalDeposited} = useStakeContext();
    const balance = useBalance(contract);
    const maxNumber = getBalanceNumber(balance);

    // 
    const [facethDeposit] = useDeposit();

    const sendFn = useSendModal(facethDeposit);
    const handleLogin = useCallback(async (data: any) => {
        let _number = getMaxNumber(data['amount'], new BigNumber(balance));
        await sendFn(pid, _number)
    }, [balance, pid]);

    return (
        <GridStyle>
            <div className="inner">
                <div>


                    <div className={'padding'}>
                        <InputWidget label={'Amount to stake'}
                                     name={'amount'}
                                     setValue={(ev) => {
                                         setValue(ev, maxNumber)
                                     }}
                                     errors={errors}
                                     inputRef={register({
                                         required: 'no Enter the number is dame',
                                         max: {value: maxNumber, message: `maxNumber ${maxNumber}`}
                                     })}
                                     placeholder={'0.00'}
                        />
                    </div>
                    <StakeButtonWidget
                        onClick={handleSubmit(handleLogin)}
                        text={'Confirm Deposit'}
                    />


                    <div className={'data_show'}>
                        <TopStyle>
                            <p>My Pool Share</p>
                            <span>{deposits.div(totalDeposited).times(100).toFixed(2)}</span>
                        </TopStyle>
                        <TopStyle>
                            <p>Available</p>
                            <span>{getBalanceFormat(balance)}</span>
                        </TopStyle>
                    </div>
                    <div className={'bottom'}>
                        <a target="_blank"
                           href={`https://app.uniswap.org/#/add/v2/0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa/${address}`}>Go
                            to Uniswap Pool</a>
                    </div>
                    <BackWidget/>

                </div>
            </div>


        </GridStyle>
    );
}

export const GridStyle = styled.div`

  .inner {
    padding: 0 10px 10px 10px;
    > div {
      width: 100%;
    }
    > div {
      margin-right: 25px;
    }


    .data_show {
      display: flex;
      justify-content: space-between;
      > div {
        flex: 1;
      }
    }


    .bottom {
      margin: 20px 0 60px 0;
      text-align: end;
      a {
        color: ${({theme}) => theme.color.greens[100]};
        text-decoration: none;
        font-size: 14px;
      }
    }
  }


`

export const TopStyle = styled.div`
  p {
    font-weight: 400;
    font-size: 18px;
    color: #2A1D69;
    line-height: 27px;
  }
  span {
    font-size: 21px;
    font-weight: bold;
    color: #2A1D69;
    line-height: 31px;
  }
`
