import {useWeb3} from "../../provider/web3_provider";
import styled from "styled-components";
import {useStakeContext} from "../../provider/stake_provider";
import {useModel} from "../../hook/use_model";
import {SelectPage} from "./select_page";
import React from "react";
import {TokenInterFace} from "../../web3/token.config";

export const SelectTopWidget = () => {
    const {tokenMap} = useWeb3();
    const {setKey,key} = useStakeContext();
    const tokenItem: TokenInterFace = tokenMap[key];

    const [handlePresent] = useModel(<SelectPage setValue={setKey}/>);

    return (
        <SelectStyle>
            <p>Protocol</p>
            <span/>
            <h3 onClick={handlePresent}>{tokenItem.nick}</h3>
        </SelectStyle>
    );
}


const SelectStyle = styled.div`
  border-radius: 14px;
  border: 1px solid #9691B580;
  padding: 10px 40px;
  display: flex;
  align-items: center;
  margin: 18px 0 28px 0;
  p {
    font-size: 21px;
    font-weight: 500;
    color: #2E1F73FF;
    line-height: 31px;
  }

  span {
    display: block;
    background-color: #847DA669;
    width: 2px;
    height: 30px;
    margin: 0 60px 0 40px;
  }

  h3 {
    color: #847DA6FF;
    font-size: 18px;
    font-weight: 500;
  }
`;
