import React from "react";
import styled from "styled-components";
import {useHistory} from "react-router-dom";
import backImg from '../../assets/images/back.png'

export const TitleWidget: React.FC<{ title: string, isBack: string }> = ({title, isBack}) => {
    let history = useHistory();
    const handleClick = () => {
        history.push(isBack);
    }
    return (
        <TitleStyle onClick={() => isBack && handleClick()}>
            {
                isBack && <img src={backImg}/>
            }
            {title}
        </TitleStyle>
    );
}


const TitleStyle = styled.div`
  padding: 10px 31px;
  box-sizing: border-box;
  border-bottom: 1px solid #E7E9ED;
  display: flex;
  align-items: center;

  font-size: 18px;
  font-weight: 400;
  color: #090B4B;

  img {
    display: block;
    width: 10px;
    height: 16px;
    margin-right: 10px;
  }
`


export const TitleTextWidget: React.FC<{ text: string }> = ({text}) => {
    return (
        <TitleTextStyle>{text}</TitleTextStyle>
    )
}

const TitleTextStyle = styled.h2`
  color: #2A1D69FF;
  font-size: 18px;
  padding: 15px 20px;
  border-bottom: 3px solid #F7FAFFFF;
`
