import React, {useState} from "react";
import styled from "styled-components";
import {useWeb3} from "../../provider/web3_provider";
import {useGetTokneAddress} from "../../server/sunder";
import {TokenInterFace} from "../../web3/token.config";
import {SelectTopWidget} from "../../widget/slect_tokens/select_top";
import {WidthStyle} from "../../widget/style/width_style";
import rewardsImg from "../../assets/images/rewards.png";
import {ButtonWidget} from "../../widget/base/button";
import {useUserInfo} from "../../server/chef";
import {useBalance} from "../../utils/token_server";
import {StakeContext} from "../../provider/stake_provider";
import {getBalanceFormat} from "../../utils/formatBalance";
import {Redirect, Route, Switch} from "react-router-dom";
import {MainPage} from "../mint/mint";
import {PreviewWidget} from "./preview";
import {DepositPage} from "./deposit";


export const StakePage: React.FC = () => {

    const {tokenMap, contrcts} = useWeb3();

    const [key, setKey] = useState(Object.keys(tokenMap)[0]);

    const tokenItem: TokenInterFace = tokenMap[key];
    const tokens = useGetTokneAddress(tokenItem.address);
    const stakeBalance = useUserInfo(tokenItem.dpid);
    const balance = useBalance(tokenItem.dContract, contrcts.chefAddress);
    return (
        <StakeContext.Provider value={{
            address: tokens.token1,
            totalDeposited: balance,
            contract: tokenItem.dContract,
            pid: tokenItem.dpid,
            lpAddr: tokenItem.dToken,
            key,setKey,
            ...stakeBalance
        }}>
            <WapStyle>
                <Switch>

                    <Route path="/stake/preview" component={PreviewWidget}/>
                    <Route path="/stake/deposit" component={DepositPage}/>

                    <Redirect to="/stake/preview"/>
                </Switch>

            </WapStyle>
        </StakeContext.Provider>
    );
}


const WapStyle = styled.div`
  padding-bottom: 20px;
`




