import React from "react";
import styled from "styled-components";
import {useAllowance, useApprove} from "../../../utils/token_server";
import {useModel} from "../../../hook/use_model";
import {SendModal} from "../../../widget/modal/send_modal";
import {Contract} from "web3-eth-contract";
import {ButtonStyle, ButtonWidget} from "../../../widget/base/button";
import {useStakeContext} from "../../../provider/stake_provider";
import {useWeb3} from "../../../provider/web3_provider";

export const StakeButtonWidget: React.FC<{ text: string, onClick?: Function }> = ({
                                                                                                                                                    text,
                                                                                                                                                    onClick,


                                                                                                                                                }) => {
    const { contract,lpAddr} = useStakeContext();
    const {contrcts} = useWeb3();

    const { chefAddress} = contrcts;

    const allowance = useAllowance(contract, chefAddress); // 
    const [approve] = useApprove(contract, lpAddr, chefAddress); // 

    const [handleApprove] = useModel(<SendModal confirmationFn={approve}/>); // 
    return (
        <WapStyle>
            {allowance <= 0 ?
                <ButtonStyle isDisable={false} onClick={handleApprove}>Approve</ButtonStyle> :  <ButtonStyle isDisable={false} onClick={
                () => onClick && onClick()
            }>
                {text}
            </ButtonStyle>}
        </WapStyle>

    );
}

const WapStyle = styled.div`
    margin: 20px 0 40px 0;
`
