import {SelectTopWidget} from "../../widget/slect_tokens/select_top";
import {getBalanceFormat} from "../../utils/formatBalance";
import {ButtonWidget} from "../../widget/base/button";
import React from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {useStakeContext} from "../../provider/stake_provider";
import {TokenInterFace} from "../../web3/token.config";
import {useWeb3} from "../../provider/web3_provider";
import {useHistory} from "react-router-dom";

export const PreviewWidget = () =>{
    const {totalDeposited,amount,deposits,key} = useStakeContext();
    const {tokenMap} = useWeb3();
    const tokenItem: TokenInterFace = tokenMap[key];
    const history = useHistory();
    return (
        <WapStyle>
            <SelectTopWidget/>
            <MainStyle>
                <div className="wap">
                    <LogoWidget name={'d' + tokenItem.stakeName + '/DAI Uniswap LP'} img={tokenItem.logo}/>
                    <CellWidget s1={'1196.5%'} s2={'Pool APY'}/>
                    <CellWidget s1={getBalanceFormat(totalDeposited)} s2={'Total Deposited'}/>
                    <CellWidget s1={getBalanceFormat(amount)} s2={'My Deposits'} img={true}/>
                    <CellWidget s1={`${getBalanceFormat(deposits)} SGT`} s2={'My Rewards'}/>
                    <div className="button">
                        <ButtonWidget text={'Approve'} onClick={()=>{
                            history.push("/stake/deposit");
                        }}/>
                    </div>
                </div>
            </MainStyle>
        </WapStyle>
    );
}



const CellWidget: React.FC<{ s1: string, s2: string, img?: boolean }> = ({s1, s2, img}) => {
    return (
        <CellStyle>
            <div>
                <h2>{s1}</h2>
                <h3>{s2}</h3>
            </div>

        </CellStyle>
    );
}
const LogoWidget: React.FC<{ name: string, img: string }> = ({name, img}) => {
    return (
        <CellStyle>
            <div>
                <h1>
                    <img src={img} alt=""/>
                    <span>{name}</span>
                </h1>
                <h3>Rewards Pool</h3>
            </div>
        </CellStyle>
    );
}

const WapStyle = styled.div`
  ${() => WidthStyle.MinWidth};
  
`
const CellStyle = styled.div`
  padding: 15px 30px;
  box-sizing: border-box;
  border-bottom: 2px solid #F7FAFFFF;
  display: flex;
  align-items: center;
  position: relative;

  div {
    h1 {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      line-height: 1;

      img {
        display: block;
        width: 40px;
        margin-right: 10px;
      }

      span {
        font-size: 18px;
        color: #2E1F73FF;
        line-height: 28px;
        font-weight: 400;
      }
    }

    h2 {
      line-height: 1;
      font-size: 18px;
      font-weight: bold;
      color: #2A1D69FF;
      margin-bottom: 13px;
    }

    h3 {
      line-height: 1;
      font-size: 16px;
      font-weight: 400;
      color: #2A1D69FF;
    }
  }

  .tiar {
    position: absolute;
    display: block;
    width: 33px;
    right: 60px;
  }
`



const MainStyle = styled.div`
  ${() => WidthStyle.MinInnerStyle};

  .wap {
    background-size: 100% 100%;
    padding: 10px 0;
  }

  .button {
    padding: 30px 50px 20px 50px;
    box-sizing: border-box;
  }
`
