import React, {useCallback} from "react";
import styled from "styled-components";
import {useForm} from "react-hook-form";
import {useStakeContext} from "../../../provider/stake_provider";
import {useWithdraw} from "../../../server/chef";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useSendModal} from "../../../widget/modal/send_modal";
import {ButtonWidget} from "../../../widget/base/button";
import {InputWidget} from "../../../widget/base/input";
import {BackWidget} from "./back_widget";

export const BottomLeft: React.FC = () => {
    const {register, errors, setValue, handleSubmit} = useForm();

    const {pid, amount, address} = useStakeContext();
    const maxNumber = getBalanceNumber(amount);

    // 
    const [facethDeposit] = useWithdraw();

    const sendFn = useSendModal(facethDeposit);
    const handleLogin = useCallback(async (data: string) => {
        let _number = getMaxNumber(data, amount);
        await sendFn(pid, _number)
        setValue('amount', 0);
    }, [pid, amount],);
    return (
        <GridStyle>
            <div className="inner">
                <div>
                    <InputWidget label={'Amount to Unstake'}
                                 name={'amount'}
                                 setValue={(ev) => {
                                     setValue(ev, maxNumber);
                                 }}
                                 errors={errors}
                                 inputRef={register({
                                     required: 'no Enter the number is dame',
                                 })}
                                 placeholder={'0.00'}
                    />
                </div>
                <div className={'padding'}>
                    <ButtonWidget onClick={handleSubmit(({amount}) => handleLogin(amount))}
                                  text={"Confirm Withdraw & Claim Rewards"}/>
                </div>


                <div className={'bottom'}>
                    <p>Rewards</p>
                    <ButtonWidget onClick={() => handleLogin('0')} text={"Claim Rewards"}/>
                </div>


                <BottomStyle>
                    <div>
                        <p>Available</p>
                        <span>{getBalanceFormat(amount)}</span>
                    </div>
                    <a target="_blank"
                       href={`https://app.uniswap.org/#/add/v2/0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa/${address}`}>Go
                        to Uniswap Pool</a>
                </BottomStyle>
                <BackWidget/>
            </div>
        </GridStyle>
    );
}

const GridStyle = styled.div`
  padding: 0 10px 10px 10px;
  .padding {
    padding: 20px 0;
  }

  .bottom {
    p {
      font-weight: 400;
      margin-bottom: 6px;
      font-size: 14px;
      color: #2A1D69;
      line-height: 27px;
    }
  }

`

export const BottomStyle = styled.div`
  margin: 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  div {
    p {
      font-weight: 400;
      font-size: 14px;
      color: #2A1D69;
      line-height: 27px;
    }

    span {
      font-size: 21px;
      font-weight: bold;
      color: #2A1D69;
      line-height: 31px;
    }
  }

  a {
    color: ${({theme}) => theme.color.greens[100]};
    text-decoration: none;
    font-size: 14px;
  }
`
