import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useMemo, useState} from "react";
import ERC20ABI from '../web3/abi/erc20.json'
import {useWallet} from "use-wallet";
import {TokenInterFace} from "../web3/token.config";

export const useConvert = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sunderAddress);
    const facethBack = useCallback(async (address: string, amount: string) => {

        let method = contrcts.sunderContract.methods.convert(address, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}

export const useRedeem = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sunderAddress);
    const facethBack = useCallback(async (address: string, amount: string) => {

        let method = contrcts.sunderContract.methods.redeem(address, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}


// token
export const useGetTokneAddress = (address: string) => {
    const {contrcts, web3,} = useWeb3();
    const [tokens, setToken] = useState<any>({
        token1: "",
        token2: "",
        token1Contract: null,
        token2Contract: null
    });
    const GetTokens = useCallback(async () => {
        let _tokens: any = {
            token1: "",
            token2: "",
        };

        _tokens['token1'] = await contrcts.sunderContract.methods.token1(address).call();
        _tokens['token2'] = await contrcts.sunderContract.methods.token2(address).call();
        _tokens.token1Contract = new web3.eth.Contract(ERC20ABI.abi as Array<any>, _tokens['token1']);
        _tokens.token2Contract = new web3.eth.Contract(ERC20ABI.abi as Array<any>, _tokens['token2']);
        setToken(_tokens);
    }, [contrcts,
        web3,
        address]);

    useEffect(() => {
        GetTokens()
    }, [address])

    return tokens;
}

// token 
export const useGetTokenMin = (address: string) => {
    const {contrcts, web3, blockNumber} = useWeb3();
    const {account} = useWallet();
    const {token1Contract, token2Contract} = useGetTokneAddress(address);
    const [min, setMin] = useState(0);
    const facethBack = useCallback(async () => {
        if (!token1Contract) return;

        let arr = [];
        arr[0] = await token1Contract.methods.balanceOf(account).call();
        arr[1] = await token2Contract.methods.balanceOf(account).call();
        let min = Math.min.apply(null, arr);
        setMin(min);
    }, [
        contrcts,
        web3,
        address,
        token1Contract,
        token2Contract
    ])
    useEffect(() => {
        facethBack();
    }, [blockNumber, token1Contract,address])
    return min
}
