import React from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {BottomLeft} from "./widget/bottom_left";
import {BottomRight} from "./widget/bottom_right";
import {Box,Typography,} from "@material-ui/core";


interface TabPanelProps {
    children?: React.ReactNode;
    dir?: string;
    index: any;
    value: any;
}

function TabPanel(props: TabPanelProps) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}




export const DepositPage: React.FC = () => {
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue: number) => {
        setValue(newValue);
    };


    return (
        <MainStyle>
            <div className="top">
                <h2 onClick={() => handleChange(0)} className={value === 0 ? 'ac' : ""}>Deposit</h2>
                <h2 onClick={() => handleChange(1)} className={value === 1 ? 'ac' : ""}>Withdraw</h2>
            </div>
            <TabPanel value={value} index={0}>
                <BottomRight/>
            </TabPanel>
            <TabPanel value={value} index={1}>
                <BottomLeft/>
            </TabPanel>
        </MainStyle>
    );
}


const MainStyle = styled.div`
  ${() => WidthStyle.MinWidth};
  //max-width: 440px;
  ${() => WidthStyle.MinInnerStyle};
  margin: 60px auto;


  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2 {
      flex: 1;
      cursor: pointer;
      padding: 15px 0;
      box-sizing: border-box;
      text-align: center;
      color: #2A1D69FF;
      border-bottom: 2px solid #F7FAFFFF;

    }

    .ac {
      border-bottom: 2px solid #12C4EAFF;
    }
  }
`
